body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
nav{
  padding: 0 1rem;
}

.content{
  min-height: calc(100vh - 70px - 64px);
  padding: 1.5rem 0;
}

.movies {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
}

.cart{
  position: fixed;
  bottom: 2rem;
  right: 2rem;

  cursor: pointer;
  z-index: 5;
  padding: 1rem;
}

@media (min-width: 767px){
  .cart{
    top: 5rem;
    bottom:  unset;
  }
}

.basket-list{
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  background-color: #fff;
  width: 50rem;
  max-width: 90%;

  animation: show 500ms ease-in-out;
}
.basket-close {
  position: fixed;
  right: 5px;
  top: 5px;
  cursor: pointer;

  color: #fff;
}

.secondary-content{
  cursor: pointer;
  float: left;
  padding-left: 5px;
}

.secondary-close{
  margin-left: 15px;
}


@keyframes show {
  from {
    top: -70%;
    opacity: 0;
  }
  to {
    top: 50%;
    opacity: 1;
  }
}

.collection-item{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.collection-name{
  display: flex;
  align-items: center;
  font-size: 18px;
}

.collection-name i {
  margin-left: 10px;
}

.main-card{
  min-height: 180px;
}

#toast-container{
  top: 3em !important;
  animation: fade-in 250ms ease-in-out;
}

@keyframes fade-in {
  from{
    opacity: 0;
    transform: translateY(2rem);
  }
  to{
    opacity: 1;
    transform: translateY(0rem);
  }

}
